@import 'src/common/styles/sass/variables';

.title {
  margin: 100px 0 30px;
  position: relative;

    .text{
    margin: 0;
    font-family: 'Josefin Sans', sans-serif;
    font-weight: 300;
    color: $textColor;
    text-transform: capitalize;
    font-size: 36px;
    line-height: 1.2;
    letter-spacing: 5px;
  }

    &::before {
        content: '';
        position: absolute;
        bottom: -30px;
        left: 50%;
        background: $primaryColor;
        width: 55px;
        height: 1px;
        transform: translate(-50%, 50%);

    }

}






